import { paymentApi } from "lib/api";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router";
import useSWR from "swr";

export const usePayment = (useRedirects = true) => {
  const { id: paymentId } = useParams();
  const [isExpired, setIsExpired] = useState(false);
  const navigate = useNavigate();

  const paymentSwr = useSWR(
    paymentId ? "payment" : null,
    () => (paymentId ? paymentApi.fetchPayment(paymentId) : null),
    { refreshInterval: isExpired ? 0 : 1, shouldRetryOnError: false }
  );

  useEffect(() => {
    if (useRedirects === false) return;

    if (paymentSwr.data?.status === "completed") {
      navigate(`/payment/${paymentId}/success`);
      return;
    }

    const error = paymentSwr.error;
    if (error?.response.data.status === "not_found" && !!error?.response.data.expiry_at) {
      setIsExpired(true);
      return;
    }

    if (error?.response.data.status === "not_found") {
      navigate("/not-found");
      return;
    }
  }, [navigate, paymentSwr.data, paymentSwr.error, paymentId, useRedirects]);

  const handleExpired = useCallback(() => {
    setIsExpired(true);
  }, []);

  return {
    payment: paymentSwr.data,
    paymentId,
    isExpired,
    handleExpired,
  };
};
