import { Footer, Layout, Main } from "../layout";
import { block } from "bem-cn";
import "./general-layout.scss";

const b = block("pay-with-near-general-layout");

type Props = {
  className?: string;
};
export const GeneralLayout: React.FC<Props> = (props) => {
  const { className, children } = props;

  return (
    <Layout className={b.mix(className)}>
      <Main className={b("main")}>{children}</Main>
      <Footer />
    </Layout>
  );
};
