import "./layout.scss";
import { block } from "bem-cn";
import classNames from "classnames";
import React, { HTMLAttributes } from "react";

const b = block("pay-with-near-layout");
const bMain = block("pay-with-near-main");
const bFooter = block("pay-with-near-footer");

type Props = {
  className?: string;
  children: React.ReactNode;
};
export const Layout: React.FC<Props> = (props) => {
  const { children, className } = props;

  return (
    <div className={b.mix(className)}>
      <header className={b("header")}>
        <img
          className={b("logo")}
          src="https://app.coinpipe.finance/img/logo.svg"
          alt="coinpipe-logo"
        />
      </header>
      {children}
    </div>
  );
};

export const Main: React.FC<Props> = (props) => {
  return <main className={bMain.mix(props.className)}>{props.children}</main>;
};

export const Footer: React.FC<React.HTMLAttributes<any>> = ({
  className,
  ...rest
}: HTMLAttributes<HTMLDivElement>) => {
  return (
    <footer className={classNames(bFooter(), className)} {...rest}>
      <a className={bFooter("support")} href="mailto:support@coinpipe.finance">
        Support
      </a>
    </footer>
  );
};
